.page-title-wrap {
  height: 7.6rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.page-title {
  height: 3.9rem;
  font-size: 1.4rem;
  font-weight: 500;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.page-title .title-line {
  width: 2.18rem;
  height: 0.18rem;
  background-color: #e9bb5c;
  border-radius: 13px;
  outline: none;
  border: 0px;
}
.title-line-wrap {
  display: flex;
  justify-content: center;
}
.title-text-wrap {
  height: 4.2rem;
  padding: 0rem 3.12rem 1rem 3.12rem;
  text-align: center;
  font-size: 0.9rem;
  color: #345167;
}
